import React,{useState, useEffect} from "react";
import ArticlesList from '../components/ArticlesList';
import CommentList from '../components/CommentsList';
import articlesContent from "./article-content";
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
const ArticlePage = ({ match }) => {
  // console.log(props); if we don't destructure match with{} and use props
  // const name = props.match.params.name;

  const name = match.params.name;
  const article = articlesContent.find(article => article.name === name);

  const [articleInfo, setArticleInfo] = useState({upvotes:0, comments:[]});

  useEffect(()=>{
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      // console.log(result);
      if(result != null){
      const body = await result.json();
      console.log(body);
      setArticleInfo(body);
      }
    }
    fetchData();

    // setArticleInfo({upvotes:Math.ceil(Math.random()*10)})
  },[name])

  if (!article ) return <h1>This article doesn't exist</h1>

  const otherArticles = articlesContent.filter(article => article.name !== name);
  return (
    <>
      <h1>{article.title}</h1>
      {article.content.map((paragraph,key)=>(
          <p key={key}>{paragraph}</p>
      ))}
      <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
      <CommentList comments={articleInfo.comments} />
      <h3>Other Articles:</h3>
      <ArticlesList articles={otherArticles}/>
    </>
  );
};

export default ArticlePage;
