import React from "react";
import {Link} from "react-router-dom";

const Navbar = () =>(
    <nav>
        <ul>
            <li>
                <Link to="/">Home</Link>
                <Link to="/about">About</Link>
                {/* <Link to="/article">Article</Link> comment out to understand*/}
                <Link to="/articles-list">Articles List</Link>
            </li>
        </ul>
    </nav>
)

export default Navbar;