import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import HomePage from "./pages/Homepage";
import ArticlePage from "./pages/ArticlePage";
import ArticlesListPage from "./pages/ArticlesListPage";
import About from "./pages/About";
import NavBar from "./navbar";
import NotFoundPage from "./pages/NotFoundPage";
import "./App.css";

function App() {
  // const Niether = () => <h1>Not Accessable</h1>;
  //idea was to replace the empty white display with a message
  //when hit on url localhost:/article
  //but done better by the route at the last
  return (
    <Router>
      <div className="App">
        <NavBar />
        <div id="page-body">
          <Switch>
            <Route path="/" component={HomePage} exact />
            <Route path="/about" component={About} />
            <Route path="/articles-list" component={ArticlesListPage} />
            <Route path="/article/:name" component={ArticlePage} />
            {/* <Route path="/article/" component={Niether} /> 
          this was my way but the NotFoundPage fot all plus points*/}

            <Route component={NotFoundPage} />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
